import { Swiper, SwiperSlide } from 'vue-awesome-swiper' 
export default {
  name: "MintSuccess",
  components: { 
      Swiper, 
      SwiperSlide,
    },
  props: {
    showMintSuccess: {
      type: Boolean,
      default: false
    },
    MintData: {
      type: Array,
      default: () => []
    },
    sendhash: {
      type: String,
      default: ""
    }
  },
  data() {
    return {
      MintNum: 1, 
      visables: this.showMintSuccess,
      swiperOption: { // 配置说明直接看官网
        loop: false,
        pagination: {
          el: '.swiper-pagination',
          clickable: true
        },
        prevButton: '.swiper-button-prev',
        nextButton: '.swiper-button-next',
        slidesPerView: 4,
        observer: true,
        observeParents: true,
        preventClicks: false,
        slideToClickedSlide: false,
        slidesPerColumn: 1,
        slidesOffsetAfter: 150,
        swiperOption: {
          initialSlide: 0,
          slidesPerView : 2.4,  
          direction: 'horizontal',  
          loop: false,  
        }
      }
    }
  },
  watch: {
    showMintSuccess(n) {
      this.visables = n;
    },
  },
  methods: {
    close() {
      this.$emit("closeMintSuccess", false);
    }
  }, 
}
